import listItemComponent from '@/components/listItemComponent.vue';
export default {
	components: {
		listItemComponent
	},
	data() {
		return {
			rankField: 'fengyun',
			rankList: [],
			page: 1,
		};
	},
	onLoad(option) {
		this.getRankList()
	},
	onReachBottom() {
		this.page++
		this.getRankList()
	},
	methods: {
		changeField(field) {
			this.page = 1
			this.rankField = field
			this.keywords = ''
			this.getRankList()
		},
		getRankList() {
			this.$app.request(this.$app.API.STAR_RANK, {
				page: this.page,
				rankField: this.rankField,
			}, res => {
				if (this.page == 1) {
					this.rankList = res.data
				} else {
					this.rankList = this.rankList.concat(res.data)
				}
			})
		},
	},
}
